import style from '../styles/styles';
import styled from 'styled-components';

//
import gameBackground from '../assets/night.png';

//
import axios from 'axios';
// 
import { io, Socket } from 'socket.io-client';
import GameStart from '../screens/GameStart';

// local storage  
let data = localStorage.getItem('token');
let save_token = JSON.parse(data) && JSON.parse(data).access_token;
let save_refresh_token = JSON.parse(data) && JSON.parse(data).refresh_token;
let save_user_idx = JSON.parse(data) && JSON.parse(data).user_idx;
let save_user_name = JSON.parse(data) && JSON.parse(data).user_name;

console.log('  : ' + save_user_idx);

function GameRoleComponent(props) {

    const ghost = "\n     .\n     \n   .\n  3    .\n ,    \n   !!!\n   .   *^3^*";
    const human = "\n  \n  .\n     .\n  ,  .\n3      .\n 3    .\n   .   *^3^*";

    //  5 , 
    const [seconds, setSeconds] = useState(5);

    //let timer = 5;
    useEffect(() => {
        console.log('   _gamerole' + props.role);
        ///timer = props.timer;

        const countdown = setInterval(() => {
            if (parseInt(seconds) > 0) {
                setSeconds(parseInt(seconds) - 1);
            }
        }, 1000);

        return () => {clearInterval(countdown); console.log('   ')} ;
    }, [seconds]);

    return (
        <Container>
            <TxtContainer>
                {
                    props.role == "human" ?
                        <HeadLine>
                            <p>  <RoleTxt color={style.blue}>""</RoleTxt> . </p>
                            {human.split("\n").map((i, key) => {
                                if (key === 3 || key === 5)
                                    return <Content key={key}><br></br>{i}</Content>;
                                else
                                    return <Content key={key}>{i}</Content>;
                            })}
                        </HeadLine>
                        :
                        <HeadLine>
                            <p>  <RoleTxt color={style.blue}>""</RoleTxt> . </p>
                            {ghost.split("\n").map((i, key) => {
                                if (key === 3 || key === 5)
                                    return <Content key={key}><br></br>{i}</Content>;
                                else
                                    return <Content key={key}>{i}</Content>;
                            })}
                        </HeadLine>
                }

                <TimerBtnContainer>{seconds}  </TimerBtnContainer>
            </TxtContainer>
        </Container>

    );
}

const Container = styled.div`
    width: 450px;
    height: 450px;
    margin-top: 85px;
    border-width: thin;
    border-radius: 10px;
    border-color: #000000;
    border-style: solid;
`;

const TxtContainer = styled.div`
    width: 420px;
    height: 400px;
    padding: 5px;
    margin-left: 20px;
    margin-top: 20px;    
`;

const TimerBtnContainer = styled.div`
    width: 130px;
    height: 30px;
    margin-top: 15px;
    margin-left: 270px;   
    font-size: 20px;
    background-color: #fff;
    text-align: center;
    border-width: 1px;
    border-radius: 15px;
    border-color: #000;    
    border-style: solid;
`;

const HeadLine = styled.div`
    width: 100%;
    font-size: 23px;
    display: flex;  
    flex-direction: column;
    p{
        white-space: nowrap;
    }
`;

const RoleTxt = styled.text`
    font-size: 26px;
    color: ${(props) => (props.color)};
`;

const Content = styled.div`
    width: 100%;
    display: flex;    
    font-size: 23px;
`;
export default GameRoleComponent;